<template>
  <section>
    <b-row>
      <b-col>
        <b-input-group prepend="" >
          <b-form-input v-model="msg.name"/>
        </b-input-group>
      </b-col>
    </b-row>
    <b-row>
      <b-col>
        <b-input-group prepend="" >
          <b-form-input v-model="msg.img"/>
        </b-input-group>
      </b-col>
    </b-row>
    <b-row>
      <b-col>
        <b-input-group prepend="" >
          <b-form-select v-model="msg.type" :options="options"/>
        </b-input-group>
      </b-col>
    </b-row>
    <b-row>
      <b-col>
        <b-input-group prepend="" >
          <b-form-input v-model="msg.url"/>
        </b-input-group>
      </b-col>
    </b-row>
    <b-row>
      <Edit ref="editContent" v-on:updateEditContent="updateEditContent"/>
    </b-row>
    <b-row>
      <b-button class="mt-3" variant="outline-danger" block @click="pushRace"></b-button>
    </b-row>
  </section>
</template>

<script>
import Edit from '@/components/admin/edit'
export default {
  components: {
    Edit
  },
  data () {
    return {
      editContent: '',
      options: {
        0: 'pdf',
        1: '',
        2: 'B'
      },
      msg: {
        name: '',
        url: '',
        type: 0,
        img: ''
      }
    }
  },
  methods: {
    postCid (e) {
      this.content = e.content
      this.title = e.name
    },
    linkClass (idx) {
      if (this.tabIndex === idx) {
        return ['bg-primary', 'text-light']
      } else {
        return ['bg-light', 'text-info']
      }
    },
    updateEditContent (e) {
      this.editContent = e
    },
    async pushRace () {
      this.msg.content = this.editContent
      let errMsg = 0
      let msg = this.msg
      Object.values(msg).map(e => {
        if (e === '') {
          errMsg += 1
        }
        console.log(e)
      })
      console.log(errMsg)
      if (errMsg) {
        this.$store.commit('changAlert', {
          msg: '',
          status: 2,
          sec: 5
        })
      } else {
        let res = await this.$axios.put('/api/admin/contset', this.msg)
        if (res.data.status) {
          this.$store.commit('changAlert', {
            msg: '',
            status: 1,
            sec: 5
          })
        } else {
          this.$store.commit('changAlert', {
            msg: '',
            status: 2,
            sec: 5
          })
        }
      }
    }
  },
  mounted () {
  }
}
</script>

<style scoped>
.row {
  margin-bottom: 0.6rem;
}
</style>



