<template>
  <section>
    <b-container fluid class="files-container">
      <b-row  class="outputData">
        <b-col cols="6">
          <span>~</span>
        </b-col>
        <b-col cols="6">
          <b-button variant="outline-danger"></b-button>
        </b-col>
      </b-row>
      <!-- <b-row align-content="center" class="adminAdd">
        <b-col cols="6">
          <b-button v-b-toggle.collapse1 variant="outline-danger" class="m-1"></b-button>
        </b-col>
        <b-col cols="6">
          <b-button v-b-toggle.collapse2 variant="outline-danger" class="m-1"></b-button>
        </b-col>
      </b-row> -->
      <!--  -->
      <!-- <b-collapse id="collapse1" class="mt-2">
        <b-card>
          <p class="card-text"></p>
          <b-input
            v-model="addUsername"
            trim
            type="number"
            :state="addUsernameState"
            placeholder="" />
          <b-button variant="outline-danger" size="sm"></b-button>
        </b-card>
      </b-collapse> -->
      <!--  -->
      <!-- <b-collapse id="collapse2" class="mt-2">
        <b-card>
          <p class="card-text"></p>
          <b-input
            v-model="delUsername"
            trim
            type="number"
            :state="delUsernameState"
            placeholder="" />
          <b-button variant="outline-danger" size="sm"></b-button>
        </b-card>
      </b-collapse> -->

        <b-row>
          <b-card no-body>
            <b-tabs card v-model="tabIndex">
              <b-tab title="" :title-link-class="linkClass(0)">
                <Race/>
              </b-tab>
              <b-tab title="" :title-link-class="linkClass(1)">
                <Sxdwj />
              </b-tab>
              <b-tab title="" :title-link-class="linkClass(2)">
                <Xxzl />
              </b-tab>
              <b-tab title="" :title-link-class="linkClass(3)">
                <Whcp />
              </b-tab>
              <b-tab title="" :title-link-class="linkClass(4)">
                <AddRace />
              </b-tab>
              <b-tab title="" :title-link-class="linkClass(5)">
                <AddZl />
              </b-tab>
            </b-tabs>
          </b-card>

        </b-row>
    </b-container>
    <b-modal id="modal-scrollable" scrollable :title="title" ok-only ok-title="">
      {{content}}
    </b-modal>
  </section>
</template>

<script>
import Race from '@/components/admin/race'
import Sxdwj from '@/components/admin/sxdwj'
import Whcp from '@/components/admin/whcp'
import Xxzl from '@/components/admin/xxzl'
import AddRace from '@/components/admin/addRace'
import AddZl from '@/components/admin/addZl'
export default {
  components: {
    Race,
    Sxdwj,
    Whcp,
    Xxzl,
    AddRace,
    AddZl
  },
  data () {
    return {
      content: '',
      title: '',
      addUsername: '',
      delUsername: '',
      tabIndex: 0
    }
  },
  computed: {
    addUsernameState () {
      // eslint-disable-next-line
      return this.addUsername.length === 11 ? true : false
    },
    delUsernameState () {
      // eslint-disable-next-line
      return this.delUsername.length === 11 ? true : false
    }
  },
  methods: {
    postCid (e) {
      this.content = e.content
      this.title = e.name
    },
    linkClass (idx) {
      if (this.tabIndex === idx) {
        return ['bg-primary', 'text-light']
      } else {
        return ['bg-light', 'text-info']
      }
    }
  },
  mounted () {
  }
}
</script>

<style scoped>
section {
  min-height: 58vh;
}

.adminAdd, .outputData {
  padding-bottom: 0.4rem;
  /* height: 3.5rem; */
  border-bottom: solid 0.5px lightgrey;
}
.adminAdd .col-6{
  margin-top: 0.5rem;
  display: flex;
  justify-content: center;
}

#collapse2 button, #collapse1 button {
  margin-top: 0.4rem;
}
.outputData {
  height: 3.5rem;
  padding-top: 0.4rem;
}
.outputData .col-6{
  display: flex;
  justify-content: center;
  align-items: center;
}
.tab-content:focus, .tab-pane:focus {
  outline: none;
}

</style>



