<template>
  <section>
    <b-list-group>
      <div v-for="item in sxdList" :key="item.id">
        <b-list-group-item v-b-toggle="'raceid' +item.id" >
          {{item.title}}
        </b-list-group-item>
        <b-collapse :id="'raceid' + item.id" class="mt-2">
          <p>{{item.content}}</p>
          <!-- <img :src=item.img > -->
          <!-- <p><span>{{item.type}}</span></p> -->
          <p>URL{{item.url}}</p>
          <p>
            <b-button v-b-modal.raceModal @click="changeRaceId(item.id, item.title)"  variant="outline-danger" class="m-1"></b-button>
          </p>
        </b-collapse>
      </div>
      <p class="more-p">
        <b-button block @click="getMoreList()" variant="outline-danger"></b-button>

      </p>
    </b-list-group>
    <b-modal id="raceModal" scrollable :title="activeTitle" hide-footer>
      <b-button class="mt-3" variant="outline-danger" block @click="delRace"></b-button>
    </b-modal>
  </section>
</template>

<script>
import Pdf from '@/components/pdf'
export default {
  components: {
    Pdf
  },
  data () {
    return {
      sxdList: [
        {
          id: 111,
          title: 'pdf',
          content: 'sdksjdsjdlsad',
          url: 'https://qnimg.vadxq.com/demo/pdf/demo.pdf'
        },
        {
          id: 222,
          title: 'B',
          content: 'sdksjdsjdlsad',
          url:
            '<iframe src="//player.bilibili.com/player.html?aid=44211901&cid=77441895&page=1" scrolling="no" border="0" frameborder="no" framespacing="0" allowfullscreen="true"> </iframe>'
        },
        {
          id: 333,
          title: '',
          content: 'sdksjdsjdlsad',
          url: 'http://www.w3school.com.cn/i/movie.mp4'
        }
      ],
      activeId: null,
      activeTitle: null
    }
  },
  methods: {
    changeRaceId (id, title) {
      this.activeId = id
      this.activeTitle = title
    },
    async getFirList () {
      let res = await this.$axios.get('/api/data/?page=1&size=5&category=2')
      if (res.data.status) {
        this.sxdList = res.data.data
        if (this.sxdList.length === 10) {
          this.page += 1
        }
      } else {
        this.$store.commit('changAlert', {
          msg: '',
          status: 2,
          sec: 5
        })
      }
    },
    async getMoreList () {
      // let res = await this.$axios.get(`/api/data/?page=${this.page}&size=10&category=2`)
      // if (res.data.status) {
      //   this.sxdList = this.sxdList.concat(res.data.data)
      //   if (this.sxdList.length === 10 * this.page) {
      //     this.page += 1
      //   }
      // }
      this.sxdList = this.sxdList.concat(this.sxdList)
    },
    async delRace () {
      let res = await this.$axios.delete(`/api/admin/data/${this.activeId}`)
      if (res.data.status) {
        this.$store.commit('changAlert', {
          msg: '',
          status: 1,
          sec: 5
        })
      } else {
        this.$store.commit('changAlert', {
          msg: '',
          status: 2,
          sec: 5
        })
      }
    }
  }
}
</script>

<style scoped>
.more-p {
  margin-top: 1rem;
}
</style>



