<template>
  <section>
    <b-container fluid class="files-container">
      <div class="sxd-nav">
        <router-link to="/me">
          <img src="data:image/png;base64,">
          
        </router-link>
        <span> /</span>
        <img src="data:image/png;base64,">
          <span></span>
      </div>

      <b-container class="change-con">

        <b-row>
          <b-col>
            <b-input-group prepend="" >
              <b-form-input v-model="msg.name"/>
            </b-input-group>
          </b-col>
        </b-row>

        <b-row>
          <b-col>
            <b-input-group prepend="" >
              <b-form-input v-model="msg.sfzh"/>
            </b-input-group>
          </b-col>
        </b-row>

        <b-row>
          <b-col lg="6" sm="6" cols="6" md="6" xl="6">
            <b-input-group prepend="" >
              <b-form-select v-model="msg.xb" :options="options"/>
            </b-input-group>
          </b-col>

          <b-col lg="6" sm="6" cols="6" md="6" xl="6">
            <b-input-group prepend="" >
          <b-input type="number" v-model="msg.nl"  />
        </b-input-group>
          </b-col>
        </b-row>

        <b-row>
          <b-col class="selecList">
            <b-input-group prepend="" class="selectWid">
            </b-input-group>
            <b-input-group  >
              <b-form-select v-model="activityFirCode" :options="cityData.fir" @change="getSecCode()" />
            </b-input-group>

            <b-input-group >
              <b-form-select v-model="activitySecCode" @change="getThrCode()" :options="cityData.sec"  />
            </b-input-group>

            <b-input-group  >
              <b-form-select v-model="activityThrCode" :options="cityData.tir"  />
            </b-input-group>
          </b-col>
        </b-row>

        <b-row>
          <b-col>
            <b-input-group prepend="">
              <b-form-select  v-model="msg.zw" :options="zwOptions" />
            </b-input-group>
          </b-col>
        </b-row>

        <b-row>
          <b-col>
            <b-input-group prepend="" >
              <b-input  v-model="msg.zc"  />
            </b-input-group>

          </b-col>
        </b-row>

        <b-row>
          <b-col>
            <b-input-group prepend="" >
              <b-input  v-model="msg.zzmm"  />
            </b-input-group>

          </b-col>
        </b-row>

        <b-row>
          <b-col>
            <b-input-group prepend="" >
              <b-input  v-model="msg.byxx"  />
            </b-input-group>

          </b-col>
        </b-row>

        <b-row>
          <b-col>
            <b-input-group prepend="" >
          <b-form-select  v-model="msg.xl" :options="xlOptions" />
        </b-input-group>

          </b-col>
        </b-row>

        <b-row>
          <b-col>
            <b-input-group prepend="" >
              <b-input  v-model="msg.gzdw"  />
            </b-input-group>
          </b-col>
        </b-row>

        <b-row>
          <b-col>
            <b-input-group prepend="" >
              <b-input  v-model="msg.lxfs"  />
            </b-input-group>
          </b-col>
        </b-row>

        <b-row>
          <b-col>
            <b-input-group prepend="" >
              <b-input  v-model="msg.dyrs"  />
            </b-input-group>
          </b-col>
        </b-row>

        <b-row v-if="zwOpt == 0">
          <b-col>
            <b-input-group prepend="" >
              <b-input  v-model="msg.fdyrs"  />
            </b-input-group>
          </b-col>
        </b-row>

        <b-row>
          <b-col>
            <b-form-group
            description=""
              label-for="input1"
            >
            <p>:</p>
              <b-form-textarea
                v-model="msg.kh"
                placeholder=""
                rows="3"
                max-rows="6"
              />
            </b-form-group>

          </b-col>
        </b-row>
        <b-row>
          <b-col>
            <b-button block @click="pushLeave()" variant="outline-danger"></b-button>
          </b-col>
        </b-row>

      </b-container>
    </b-container>
  </section>
</template>

<script>
import province from '../../assets/json/province.json'
import country from '../../assets/json/country.json'
import city from '../../assets/json/city.json'

export default {
  data () {
    return {
      cityData: {
        fir: province,
        sec: city['360000000000'],
        tir: country['360100000000']
      },
      country: country,
      city: city,
      activityFirCode: '360000000000',
      activitySecCode: null,
      activityThrCode: null,
      dismissSecs: 5,
      dismissCountDown: 0,
      errMsg: '',
      options: {
        0: '',
        1: ''
      },
      zwOptions: {
        0: '',
        1: '',
        2: ''
      },
      xlOptions: {
        0: '',
        1: '',
        2: '',
        3: ''
      },
      msg: {
        name: '', // 
        xb: 0, // 
        nl: '', // 
        // dq: '', // 
        zw: 2, // 
        zc: '', // 
        zzmm: '', // 
        byxx: '', // 
        xl: '', // 
        gzdw: '', // 
        lxfs: '', // 
        dyrs: '', // 
        fdyrs: '', // 
        kh: '', // 
        sfzh: '' // 
      }
    }
  },
  computed: {
    zwOpt () {
      return this.msg.zw
    }
  },
  methods: {
    async pushLeave () {
      this.msg.qd = this.activityThrCode
      let errMsg = 0
      let msg = this.msg
      delete msg['fdyrs']
      Object.values(msg).map(e => {
        if (e === '') {
          errMsg += 1
        }
        console.log(e)
      })
      console.log(errMsg)
      if (errMsg) {
        this.$store.commit('changAlert', {
          msg: '',
          status: 2,
          sec: 5
        })
      } else {
        let res = await this.$axios.put('/api/user/info', this.msg)
        if (res.data.status) {
          this.$store.commit('changAlert', {
            msg: '',
            status: 1,
            sec: 5
          })
        } else {
          this.$store.commit('changAlert', {
            msg: '',
            status: 2,
            sec: 5
          })
        }
      }
    },
    async getInfo () {
      let res = await this.$axios.get('/api/user/info')
      if (res.data.status) {
        this.msg = res.data.data
        if (res.data.data.dq.length > 6) {
          this.activityFirCode = res.data.data.qd.slice(0, 2)
          this.activitySecCode = res.data.data.qd.slice(2, 4)
          this.activityThrCode = res.data.data.qd.slice(4, 6)
        }
      }
    },
    countDownChanged (dismissCountDown) {
      this.dismissCountDown = dismissCountDown
    },
    showAlert (e) {
      this.dismissCountDown = this.dismissSecs
      this.errMsg = e
    },
    getSecCode () {
      this.cityData.sec = this.city[this.activityFirCode]
    },
    getThrCode () {
      this.cityData.tir = this.country[this.activitySecCode]
    }
  },
  mounted () {
    this.getInfo()
    // this.cityData.fir = JSON.stringify(province)
  }
}
</script>

<style scoped>

.files-container {
  padding-right: 0px;
  padding-left: 0px;
  /* background-color: rgb(246, 41, 20); */
  min-height: 87vh;
  width: 100vw;
}

.sxd-nav {
  background-color: #fff;
  /* width: 100vw; */
  padding-left: 0.5rem;
  height: 2.8rem;
  display: flex;
  align-items: center;
  border-bottom: solid 1.4px rgba(0, 0, 0, 0.4);
}
.sxd-nav img {
  margin: -0.3rem 0 0 0.3rem;
}
.sxd-nav a {
  color: rgb(246, 41, 20);
}
.sxd-nav span {
  display: inline-flex;
  margin-left: 0.4rem;
}

.change-con {
  background-color: #fff;
  padding: 1.2rem 1rem;
}
.row {
  margin-bottom: 1rem;
}
.form-group p {
  font-size: 0.8rem;
  margin: 0 0 0.4rem 0;
}
.picker {
  z-index: 1000;
}
footer {
  color: #000;
}

.selecList {
  display:  flex;
}
.selectWid {
  width: 5rem;
}
</style>



