<template>
  <section class="race">
    <Navbar navText="" />
    <b-container fluid class="files-container">
      <div class="sxd-nav">
        <router-link to="/">
          <img src="data:image/png;base64,">
          
        </router-link>
        <span> /</span>
        <img src="data:image/png;base64,">
          <span></span>
      </div>
      <b-card-group deck>
        <b-card
          img-src="/static/bg.png"
          img-alt="Image"
          img-top
          :footer=item.title
          footer-tag="footer"
          @click="postCid(item)"
          :key=item.id
          class="item"
          v-b-modal.modal-scrollable
          v-for="item in raceList"
          :cid="item.id"
          no-body
        >
        </b-card>
      </b-card-group>
      <b-row>
        <b-col>
          <b-button block @click="getMoreList()" variant="outline-danger"></b-button>
        </b-col>
      </b-row>
    </b-container>
    <b-modal id="modal-scrollable" scrollable :title="title" ok-only ok-title="">
      <div v-if="type==='pdf'">
        <Pdf :url=url />
      </div>
      <div v-if="type==='bzhan'">
        <b-embed
          type="iframe"
          aspect="16by9"
          src="//player.bilibili.com/player.html?aid=44211901&cid=77441895&page=1"
          allowfullscreen
        />
      </div>
      <div v-if="type==='mp4'">
        <b-embed
          type="iframe"
          aspect="16by9"
          src="https://www.youtube.com/embed/zpOULjyy-n8?rel=0"
          allowfullscreen
        />
      </div>
    </b-modal>
    <Footer/>
  </section>
</template>

<script>
import Navbar from '@/components/navbar'
import Footer from '@/components/footer'
import Pdf from '@/components/pdf'
export default {
  components: {
    Navbar,
    Footer,
    Pdf
  },
  data () {
    return {
      raceList: [
        {
          id: 111,
          title: 'pdf',
          url: 'https://qnimg.vadxq.com/demo/pdf/demo.pdf',
          type: 'pdf'
        },
        {
          id: 222,
          title: 'B',
          url: '<iframe src="//player.bilibili.com/player.html?aid=44211901&cid=77441895&page=1" scrolling="no" border="0" frameborder="no" framespacing="0" allowfullscreen="true"> </iframe>',
          type: 'bzhan'
        },
        {
          id: 333,
          title: '',
          url: 'http://www.w3school.com.cn/i/movie.mp4',
          type: 'mp4'
        }
      ],
      url: '',
      title: '',
      type: ''
    }
  },
  methods: {
    postCid (e) {
      this.url = e.url
      this.title = e.title
      this.type = e.type
    },
    async getFirList () {
      let res = await this.$axios.get('/api/contest?page=1&size=10')
      if (res.data.status) {
        this.raceList = res.data.data
        if (this.raceList.length === 10) {
          this.page += 1
        }
      } else {
        this.$store.commit('changAlert', {
          msg: '',
          status: 2,
          sec: 5
        })
      }
    },
    async getMoreList () {
      // let res = await this.$axios.get(`/api/contest?page=${this.page}&size=10`)
      // if (res.data.status) {
      //   this.raceList = this.raceList.concat(res.data.data)
      //   if (this.raceList.length === 10 * this.page) {
      //     this.page += 1
      //   }
      // }
      this.raceList = this.raceList.concat(this.raceList)
    }
  }
}
</script>

<style scoped>
.item {
  padding: 0.5rem 1.25rem;
  margin-bottom: -1px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.125);
  /* border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem; */
}
.files-container {
  padding-right: 0px;
  padding-left: 0px;
  /* background-color: rgb(246, 41, 20); */
  min-height: 87vh;
  width: 100vw;
}
.item:first-child {
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
  display: flex;
  justify-content: space-between;
}

.item:first-child a {
  color: #000;
  font-weight: bolder;
  width: 3rem;
  text-align: center;
  margin-right: -1rem;
}
.sxd-nav {
  background-color: #fff;
  /* width: 100vw; */
  padding-left: 0.5rem;
  height: 2.8rem;
  display: flex;
  align-items: center;
  border-bottom: solid 1.4px rgba(0, 0, 0, 0.4);
}
.sxd-nav img {
  margin: -0.3rem 0 0 0.3rem;
}
.sxd-nav a {
  color: rgb(246, 41, 20);
}
.sxd-nav span {
  display: inline-flex;
  margin-left: 0.4rem;
}
.card {
  margin: 0.5rem;
  padding: 0;
}
.race footer {
  color: #000;
}
.row {
  margin: 15px 0px;
}
</style>



